body {
  padding-bottom: 30px;
  padding-left: 10px;
  padding-right: 10px;
}

.header {
  padding-top: 30px;
  display: flex;
}

.header h1 {
  font-family: Oswald;
  color: #e74c3c;
  flex-grow: 0;
  font-weight: 600;
}

.header h1 span {
  font-size: 60%;
  padding-left: 5px;
  display: inline-block;
  font-weight: 300;
}

.header .fb {
  padding-left: 40px;
  padding-top: 43px;
  flex-grow: 1;
}

.header .fb img {
  height: 15px;
  width: auto;
}

.header .navigation {
  padding-top: 40px;
}

.header .navigation a {
  color: #e74c3c;
  text-decoration: none;
  font-weight: 20px;
  font-weight: bold;
  display: inline-block;
  padding: 0px 5px;
}

.container {
  width: 900px;
  margin: 0 auto;
}

.container img {
  width: 100%;
}

.location-jumbo {
  width: 900px;
  height: 600px;
  position: relative;
}

.location-jumbo .location {
  padding: 15px;
  width: 50%;
  margin: 20px;
  background-color: rgba(255, 255, 255, 0.5);
}

.location-jumbo .location h3 {
  margin: 0;
}

.jumbo {
  width: 900px;
  height: 600px;
  background-image: url(../images/jumbo.jpg);
  background-size: cover;
}

.jumbo .text {
  width: 50%;
  color: white;
  font-family: Helvetica, Arial;
  padding: 30px;
  font-size: 18px;
  line-height: 1.25em;
  font-weight: 300;
}

.jumbo .btn {
  margin-top: 100px;
}

.date {
  padding-top: 30px;
  font-size: 18px;
}

.btn {
  background-color: rgba(0, 0, 0, 0.6);
  border: 4px #70c1b3 solid;
  color: #70c1b3 !important;
  display: inline-block;
  padding: 15px 40px;
  text-decoration: none;
}

.divided {
  display: flex;
  flex-direction: row;
  margin-left: -20px;
  margin-right: -20px;
}

.divided .part {
  width: 50%;
  padding: 20px;
}

.videoCaption {
  color: gray;
  text-align: center;
  padding: 0;
  margin: 0;
  margin-top: 10px;
  font-size: 14px;
}

.videoWrapper {
  position: relative;
  padding-bottom: 56.25%;
  /* 16:9 */
  padding-top: 25px;
  height: 0;
}

.videoWrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.who-is-behind-this {
  padding-top: 20px;
}

.who-is-behind-this .logos {
  text-align: center;
}

.who-is-behind-this .logos img {
  width: 250px;
  margin: 20px;
}

.who-is-behind-this .logos .futurice-logo img {
  padding-bottom: 25px;
}

.who-is-behind-this .logos .junction-logo img {
  width: 100px;
}

.who-is-behind-this .logos .rightware-logo img {
  width: 150px;
}

.who-is-behind-this .logos .assembly-logo img {
  padding-bottom: 20px;
}

.who-is-behind-this .logos .umbra img {
  width: 150px;
  padding-bottom: 10px;
}

.who-is-behind-this p {
  color: #646464;
  font-size: 16px;
  letter-spacing: 0.5px;
}

.who-is-behind-this img {
  width: 250px;
}

.oubs-promo {
  text-align: right;
  font-size: 14px;
  margin-bottom: 20px;
}

.browsehappy {
  text-align: center;
  margin: 0;
  background: #fff8e2;
  color: #000;
  padding: 0.5em 0;
}

.table {
  width: 100%;
}

.table td {
  padding: 4px;
}

.schedule-section {
  margin-bottom: 30px;
}

.schedule-section .table td:first-child {
  text-align: center;
  width: 100px;
}

.results:after {
  content: "";
  display: table;
  clear: both;
}

.results li {
  padding: 0;
  margin: 0;
  list-style: none;
  width: 25%;
  display: block;
  float: left;
  margin-bottom: 10px;
}

.results li .padding {
  padding: 5px;
}

.results li .image {
  background-color: darkgray;
  position: relative;
  display: block;
  padding-bottom: 56.25%;
}

.results li .image img {
  position: absolute;
}

.results li .image .gif {
  display: none;
}

.results li .image:hover .gif {
  display: block;
}

.results li .image:hover .thumbnail {
  display: none;
}

.results li .name {
  padding: 10px;
}

.results li a {
  color: black;
  text-decoration: none;
}

.results li .small {
  font-size: 10px;
}

.results li:nth-child(1), .results li:nth-child(2), .results li:nth-child(3), .results li:nth-child(4) {
  width: 50%;
}

.results li:nth-child(3) {
  clear: both;
}

.results li:nth-child(4n+1) {
  clear: both;
}

.results li span {
  color: gray;
}

#youtube-wrapper {
  position: relative;
  width: 900px;
  height: 600px;
  overflow: hidden;
  background-image: url(../images/jumbo.jpg);
  background-size: cover;
}

#youtube-wrapper #youtube {
  pointer-events: none;
  transition: opacity 1s;
  opacity: 0;
}

#youtube-wrapper .bg-overlay {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.3);
}

#youtube-wrapper .text {
  position: absolute;
  top: 0px;
  width: 50%;
  color: white;
  font-family: Helvetica, Arial;
  padding: 30px;
  font-size: 18px;
  line-height: 1.25em;
  font-weight: 300;
}

#youtube-wrapper .disclaimer {
  position: absolute;
  font-size: 10px;
  bottom: 20px;
  left: 30px;
}

#youtube-wrapper .disclaimer a {
  color: #fafafa;
  text-decoration: none;
}
